<template>
  <div id="app" >
    <div class="container">
      <section class="wrap" >
        <makechoice v-on:getIndex="captureIndex"
          v-for="item in choice" :key='item'
          v-bind:message='item'
          ref='xxx'
        ></makechoice>
      </section>
      <section class="tab">
        <div class="reRandom"><button  @click="reRandom">random</button></div>
        <div class="submit"><button  @click="submitSelect">submit</button></div>
        <div class="input"><input type="textarea" v-model="stringify"></div>
      </section>
      
    </div>
  </div>
</template>

<script>
import makechoice from './components/makechoice.vue'

export default {
  name: 'app',
  components: {
    makechoice
  },
  data(){
    return {
      choice:['drink','cake','food'],
      confirming:{},
      stringify:''
    }
  },
  methods:{
    captureIndex:function(n, index){
      this.confirming[index] = n
    },
    submitSelect(){
      for(let i = 0; i < this.choice.length;i++){
        if(this.confirming[this.choice[i]] === undefined){
          alert('请勾选全部选项')
          return
        }
      }
      this.stringify = JSON.stringify(this.confirming)
      alert(this.stringify )
    },
    reRandom(){ 
      for(let i =0;i<3;i++){
        let randomNum = Math.ceil(Math.random()*50) 
        let j=0
        while(j<randomNum){//这里为什么总是走相同步数？调用的次数不同啊。
          this.$refs.xxx[i].move(200)
          j++
        }
      }
    }
  }
}
</script>
